:root {
  --thick-border: 2px solid #999;
}

body {
  font-size: max(2vw, 2vh, 16px);
  font-family: 'Montserrat', sans-serif;
}

button {
  font-size: max(2vw, 2vh, 16px);
  padding: 0.5rem 1rem;
}

.App {
  text-align: center;
}
.header {
  display: flex;
  align-items: center;
  gap: 3rem;
}

header {
  button {
    border: 1px solid #ccc;
    margin: 2px;
    border-radius: 1.4rem;
  }
  button:hover,
  .active {
    background: #0a0;
    color: white;
  }
}

.board {
  display: grid;
  grid-template-columns: repeat(1fr, 9);
  grid-template-rows: repeat(1fr, 9);
  max-width: 100vh;
  max-height: 100vw;
  margin: 0 auto;
}

.cell {
  border: 1px solid #eee;
  padding: 1.2rem 1rem;
  cursor: pointer;
  .value {
    padding: 1rem;
  }
  &:nth-child(3n) {
    border-right: var(--thick-border);
  }

  &.active {
    color: #080;
    font-weight: bold;
    font-size: 1.2em;
    background: #efe;
  }

  .candidates {
    color: rgb(5, 80, 192);
    font-size: 1rem;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    font-family: 'Patrick Hand SC', cursive;

    span {
      flex-basis: 33%;
    }
  }
}

.r-1 {
  border-top: var(--thick-border);
}

.c-1 {
  border-left: var(--thick-border);
}

.r-3,
.r-6,
.r-9 {
  border-bottom: var(--thick-border);
}

@for $i from 1 through 9 {
  .r-#{$i} {
    grid-row: #{$i};
  }
  .c-#{$i} {
    grid-column: #{$i};
  }
}
